.droppable {
  position: absolute;
  width: 40%;
  height: 100%;
  right: 0;
  top: 10;
  background-color: #9E9E9E;
  border-left: 2px dotted red;
}

.draggable {
  width: 100%;
  height: 30px;
  background-color: white;
  margin: 10px auto;
}

.draggable:hover{
  background-color: navy;
  color :white;
  cursor: pointer
}

.inProgress {
  position: absolute;
  width: 40%;
  height: 100%;
  left: 0;
  top: 10;
  background-color: #EEEEEE;
  border-right: 2px dotted red;
}

.buttonContainer {
  /* make element inline flex-container */
  /* this will make its children flex-items */
  position:absolute;
  left:48%;
   top:50%;
}
